'use strict';
const Banner = React.createClass({
	render: function () {
		var classes = classNames('layout-column flex', this.props.className, {
			'cant': !this.state.show,
			'see': !this.state.show,
			'me': !this.state.show
		});

		return <div className={ classes } onLoad={ this.handleLoad }></div>;
	},

	handleLoad: function () {
		var self = this;
		this.hide();
		setTimeout(function () {
			self.show()
		}, 200);
	},

	hide: function () {
		this.state.show = false;
	},
	show: function () {
		this.state.show = true;
	}
});